'use client'

import { useEffect } from 'react'
import { Button } from '@/components/ui/button'
import Link from 'next/link'

export default function CoursesError({
  error,
  reset,
}: {
  error: Error & { digest?: string }
  reset: () => void
}) {
  useEffect(() => {
    console.error('Courses page error:', error)
  }, [error])

  return (
    <div className="container mx-auto px-4 py-16">
      <div className="text-center max-w-md mx-auto space-y-6">
        <h1 className="text-3xl font-bold text-destructive">Course Error</h1>
        <p className="text-muted-foreground">
          We encountered an error while loading the courses. This might be due to a network issue or server problem.
        </p>
        <div className="flex flex-col sm:flex-row gap-3 justify-center">
          <Button onClick={reset} variant="default">
            Try Again
          </Button>
          <Button asChild variant="outline">
            <Link href="/">Return Home</Link>
          </Button>
        </div>
      </div>
    </div>
  )
}